import React from 'react';
import Image from '../../components/image';

export default () => (
    <>
        <section className="section">
            <div className="internship-header" style={{backgroundColor: '#2d6edc'}}>

                <div className="internship-header-company">
                    <img src="../images/logo-ecf.png" />
                    <h2>ECF Bouscaren</h2>
                    <span>Stage de dveloppement du 7 janvier 2019 au 15 fvrier 2019</span>
                </div>
                
                <div>
                    <p><b>ECF Bouscaren</b> est une structure cre en 1985 et spcialise dans la formation des permis de conduire et propose galement des 
                    formations spcifiques comme les stages dco-conduite, de rcupration de points, lhandiconduite ou encore le perfectionnement des
                    sniors.</p>
                    <p>Cette entreprise est situe  Montpellier (34000), ou encore  Nmes (30000) et  Lunel-Viel (34400).</p>
                </div>
                
                <div className="internship-header-tags">
                    <span className="internship-tag-item">
                        <i className="ion ion-md-code-working"></i>Dveloppement
                    </span>

                    <span className="internship-tag-item">
                        <i className="ion ion-md-tablet-landscape"></i>Design
                    </span>

                    <span className="internship-tag-item">
                        <i className="ion ion-md-people"></i>Gestion de projet
                    </span>

                    <span className="internship-tag-item">
                        <i className="ion ion-md-cog"></i>Testing
                    </span>
                </div>

                <div className="internship-header-actions">
                    <a href="../ressources/rapport_ecf.pdf" target="_blank" className="btn btn-white">Tlcharger le rapport</a>
                    <a href="http://116.203.23.205:81/" className="btn btn-outline btn-white">
                        Voir le site<i className="ion ion-md-arrow-forward"></i></a>
                </div>
            </div>
        </section>

        <section className="section internship-content">
            <div className="container">
    
                <div className="grid grid-e">
                    
                    <aside className="article">
                        <h3>Lettre de mission</h3>

                        <div className="article-flow">
                            <p>Lobjectif de la plateforme de gestion tant de pouvoir amliorer la vitesse et la simplification de gestion des lves, des formations, 
                                ainsi que lemploi du temps, plusieurs systmes sont demands:</p>
                            <ul className="list">
                                <li>Pouvoir enregistrer/supprimer/modifier des moniteurs</li>
                                <li>Pouvoir enregistrer/supprimer/modifier des secrtaires (Administratif)</li>
                                <li>Pouvoir enregistrer/supprimer/modifier des lves</li>
                                <li>Grer les formations</li>
                                <li>Grer les cours des lves (Prvisualisation planning)</li>
                                <li>Systme de mot de passe oubli</li>
                            </ul>

                            <p>La demande porte galement sur la mise en place dune application mobile dans le futur qui sera dvelopp plus tard aprs le dveloppement
                                complet et modification apport  lapplication dveloppe lors de ce stage.</p>
                        </div>
                    </aside>

                    <Image src="../images/ecf/login.png" />
                </div>

                <div className="grid grid-images grid-e">

                    <Image src="../images/ecf/student-list.png" />
                    <Image src="../images/ecf/student-add.png" />
                </div>
            </div>
        </section>

        <section className="section section-f internship-content">
            <div className="container">

                <div className="grid grid-e">

                    <div className="internship-technology-item">

                        <div className="internship-technology-icons">
                            <img src="../images/icons/reactjs.svg" />
                        </div>

                        <p>ReactJS est une librairie JavaScript, qui permet vritablement de rvolutionner le dveloppement des interfaces pour les
                            applications web</p>
                    </div>

                    <div className="internship-technology-item">

                        <div className="internship-technology-icons">
                            <img src="../images/icons/nodejs.svg" />
                            <img src="../images/icons/apollo.svg" />
                            <img src="../images/icons/graphql.svg" />
                        </div>

                        <p>Le serveur est sous NodeJS. Jutilise une librairie ApolloServer permettant la cration simplifi dun serveur utilisant GraphQL
                            ainsi que doutils pour faciliter le dveloppement.</p>
                    </div>
                </div>
            </div>
        </section>
        
        
        <section className="section container internship-content">

            <aside className="article">
                    <h3>Systme d'email</h3>

                    <div className="article-flow">
                        <p>Lors de la cration d'un compte lve, moniteur, ou administrateur, le systme envoie un mail de confirmation de cration de compte avec 
                            les paramtres tels que le login et le mot de passe.</p>
                        <p>Le systme de mailing se base sur <b>nodemailer</b> qui permet de simplement envoyer des mails aux utilisateurs. Une template a t cre pour
                            l'ensemble des mails de l'ECF Bouscaren respectant les normes HTML.</p>
                    </div>
                </aside>

            <div className="grid grid-e">
                
                <Image src="../images/ecf/account-email.png" className="figure-center" />
                <Image src="../images/ecf/password-email.png" className="figure-center" />
                <Image src="../images/ecf/confirm-email.png" className="figure-center" />
                
            </div>

            <div className="grid grid-images grid-e">

                <Image src="../images/ecf/student-item.png" />
                <Image src="../images/ecf/student-add.png" />
            </div>
        </section>
    </>
)
